import store from '@/store'
import eventBus from '@/utils/eventBus'

const ctrlKey = 17,
  vKey = 86, // 
  cKey = 67, // 
  xKey = 88, // 

  yKey = 89, // 
  zKey = 90, // 

  gKey = 71, // 
  bKey = 66, // 

  lKey = 76, // 
  uKey = 85, // 

  sKey = 83, // 
  pKey = 80, // 
  dKey = 68, // 
  deleteKey = 46, // 
  eKey = 69 // 

export const keycodes = [66, 67, 68, 69, 71, 76, 80, 83, 85, 86, 88, 89, 90]

// 
const basemap = {
  [vKey]: paste,
  [yKey]: redo,
  [zKey]: undo,
  [sKey]: save,
  [pKey]: preview,
  [eKey]: clearCanvas,
}

// 
const lockMap = {
  ...basemap,
  [uKey]: unlock,
}

// 
const unlockMap = {
  ...basemap,
  [cKey]: copy,
  [xKey]: cut,
  [gKey]: compose,
  [bKey]: decompose,
  [dKey]: deleteComponent,
  [deleteKey]: deleteComponent,
  [lKey]: lock,
}

let isCtrlDown = false
// 
export function listenGlobalKeyDown() {
  window.onkeydown = (e) => {
    const { selection } = store.state
    if (e.keyCode == ctrlKey) {
      isCtrlDown = true
    } else if (e.keyCode == deleteKey && selection) {
      store.commit('DEL_COMPONENT')
      store.commit('RECORD')
    } else if (isCtrlDown) {
      if (!selection || !selection.isLock) {
        e.preventDefault()
        unlockMap[e.keyCode] && unlockMap[e.keyCode]()
      } else if (selection && selection.isLock) {
        e.preventDefault()
        lockMap[e.keyCode] && lockMap[e.keyCode]()
      }
    }
  }

  window.onkeyup = (e) => {
    if (e.keyCode == ctrlKey) {
      isCtrlDown = false
    }
  }
}

function copy() {
  store.commit('COPY')
}

function paste() {
  store.commit('PASTE')
  store.commit('RECORD')
}

function cut() {
  store.commit('CUT')
}

function redo() {
  store.commit('REDO')
}

function undo() {
  store.commit('UNDO')
}

function compose() {
  if (store.state.areaData.components.length) {
    store.commit('compose')
    store.commit('RECORD')
  }
}

function decompose() {
  const selection = store.state.selection
  if (selection && !selection.isLock && selection.component == 'Group') {
    store.commit('decompose')
    store.commit('RECORD')
  }
}

function save() {
  eventBus.$emit('save')
}

function preview() {
  eventBus.$emit('preview')
}

function deleteComponent() {
  if (store.state.selection) {
    store.commit('DEL_COMPONENT')
    store.commit('RECORD')
  }
}

function clearCanvas() {
  eventBus.$emit('clearCanvas')
}

function lock() {
  store.commit('LOCK')
}

function unlock() {
  store.commit('UNLOCK')
}
