// 
const events = {
    redirect(url) {
        if (url) {
            if (url.includes('http://')) {
                window.open(`${url}`)
            }
            window.open(`http://${url}`)
        }
    },

    alert(msg) {
        if (msg) {
            alert(msg)
        }
    },
}

const mixins = {
    methods: events,
}

const eventList = [
    {
        key: 'redirect',
        label: '',
        event: events.redirect,
        param: '',
    },
    {
        key: 'alert',
        label: 'alert ',
        event: events.alert,
        param: '',
    },
]

export {
    mixins,
    events,
    eventList,
}